.bg-img {
  position: absolute;
  height: 100vh;
  width: 100%;
  background: url(/img/bg.jpg);
  background-position: right;
  background-size: cover;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
}
.shadow-box {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #00000063;
}
.center-box {
  position: absolute;
  z-index: 10;
  text-align: center;
  top: 10%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  left: 50%;
}
.center-box h1,
.center-box h3 {
  margin: 0;
}
.center-box h1 {
  font-family: 'Ruthie', cursive;
  color: #a2bbce;
  text-shadow: 1px 1px 0px #424040;
  font-size: 4.8em;
}
  .center-box h1 span {
      font-size: 0.5em;
  }
.center-box h3 {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 100;
  color: #a2bbce;
  text-shadow: 1px 1px 0px #424040;
}
  .center-box h3:hover{
      color: #a2bbce;
      transition: color 0.5s ease-out;
  }


nav{
    position:fixed;
    top:0;
    left:-250px;
    width:250px;
    height:100%;
    margin: 0;
    transition:all 200ms ease-in;
    z-index: 11;
}
nav.menu-visible { left: 0px; }
nav ul {
    padding:0;
    padding-top: 20px;
    width:190px;
    height:100%;
    margin:0;
    list-style:none;
    background:#03071de6;
    overflow:hidden;
}
nav li{
    margin:0;
    text-align: right;
}
nav a{
    color:#fff;
    font-size:1em;
    text-decoration:none;
    padding:12px 15px;
    font-weight:300;
    display: block;
    text-align: right;
}
button.menu-close {
    position: absolute;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    margin: 15px;
}
svg {
    width: 18px;
    height: 18px;
}
svg line {
    stroke: #a7a3a3;
    stroke-width: 1px;
}
svg:hover line {
    stroke: #fff;
    stroke-width: 2px;
}
body, html {

}
body {
  padding:0;
  margin:0;
  font-family: 'Bitter', serif;

}
#root, .wrapper, section {
  min-height: 100%;
  padding-bottom: 30px;
}

section {
  position: relative;
  transition:all 200ms ease-in;
}
button:focus {outline:0;}
.menu-button {
  position: fixed;
  z-index: 10;
  border: 0;
  cursor: pointer;
  border-radius: 5px;
  background:#03071db3;
  color: #cac8c8;
  padding: 5px 12px;
  top: 20px;
  left: 20px;
}
.menu-button:hover {
  background: #03071d;
  color: #fff;
}
.hide { display: none; }

.img-cover {
  width: 100%;
  height: auto;
}
h1 {
  font-size: 4.5em;
  padding: 20px;
  margin-bottom: 0;
  text-align: center;
  font-family: 'Ruthie', cursive;
}
.content-page {
  margin: 0 auto;
  max-width: 800px;
  padding: 50px;
}

h2 {
  text-align: center
}

li {
  list-style: none;
  margin-bottom: 15px;
}
a { color: #a2bbce;}
p { margin-bottom: 30px; }
